// src/hooks/useSessionTimeout.js - VERSO CORRIGIDA COM DEBUG
import { useState, useEffect, useCallback, useRef } from 'react';
import { useAuth } from '../context/AuthContext';
import apiService from '../services/apiService';

const useSessionTimeout = () => {
  const { logout, isAuthenticated } = useAuth();
  
  // Estados bsicos
  const [showWarning, setShowWarning] = useState(false);
  const [timeLeft, setTimeLeft] = useState(120); // 2 horas em minutos
  const [isEditingActive, setIsEditingActive] = useState(false);
  
  // Refs para controle
  const isLoggingOut = useRef(false);
  const sessionCheckActive = useRef(true);
  const lastCheckTime = useRef(0);
  const consecutiveErrors = useRef(0);

  // Verificar status da sesso com controle de frequncia
  useEffect(() => {
    if (!isAuthenticated || isLoggingOut.current) {
      sessionCheckActive.current = false;
      return;
    }

    sessionCheckActive.current = true;
    consecutiveErrors.current = 0;

    const checkSession = async () => {
      // Evitar verificaes muito frequentes
      const now = Date.now();
      const timeSinceLastCheck = now - lastCheckTime.current;
      const minInterval = 120000; // 2 minutos
      
      if (timeSinceLastCheck < minInterval) {
        console.log(` Verificao de sesso ignorada - muito recente (${Math.round(timeSinceLastCheck/1000)}s atrs)`);
        return;
      }

      // Se j est fazendo logout ou no est autenticado, no verificar
      if (isLoggingOut.current || !sessionCheckActive.current || !isAuthenticated) {
        console.log(' Verificao cancelada - logout em andamento ou no autenticado');
        return;
      }

      console.log(' Iniciando verificao de sesso...');
      lastCheckTime.current = now;

      try {
        // Verificar se temos token antes de fazer a requisio
        const token = apiService.getToken();
        if (!token) {
          console.error(' Token no encontrado no localStorage');
          throw new Error('Token no encontrado');
        }

        console.log(` Fazendo requisio para session-status (token presente: ${token.substring(0, 20)}...)`);
        const response = await apiService.get('/auth/session-status');
        
        console.log(' Resposta da verificao de sesso:', {
          success: response.success,
          expires_in: response.expires_in,
          warning: response.warning,
          time_left_minutes: response.time_left_minutes
        });
        
        if (response.success) {
          consecutiveErrors.current = 0; // Reset contador de erros
          
          const minutesLeft = response.time_left_minutes || Math.floor(response.expires_in / 60);
          setTimeLeft(minutesLeft);
          
          console.log(` Tempo restante: ${minutesLeft} minutos`);
          
          // Mostrar aviso se restam menos de 30 minutos E no est editando
          if (response.warning && !showWarning && !isEditingActive) {
            console.log(' Sesso expirando em breve, mostrando aviso');
            setShowWarning(true);
          } else if (!response.warning && showWarning) {
            console.log(' Sesso no est mais na zona de aviso, ocultando');
            setShowWarning(false);
          }
        } else {
          console.error(' Resposta de sesso invlida:', response);
          throw new Error(response.message || 'Resposta invlida do servidor');
        }
        
      } catch (error) {
        consecutiveErrors.current++;
        console.error(` Erro ao verificar sesso (tentativa ${consecutiveErrors.current}):`, {
          message: error.message,
          status: error.status,
          name: error.name
        });
        
        // S fazer logout aps mltiplos erros consecutivos
        if (consecutiveErrors.current >= 5) {
          console.log(' Mltiplos erros de sesso detectados, fazendo logout...');
          
          // Se for erro de autenticao e no est j fazendo logout
          if (!isLoggingOut.current && 
              (error.message.includes('Sesso') || 
               error.message.includes('Token') || 
               error.message.includes('401') ||
               error.message.includes('expirada'))) {
            
            console.log(' Sesso expirada detectada, fazendo logout...');
            isLoggingOut.current = true;
            sessionCheckActive.current = false;
            
            try {
              await logout();
            } catch (logoutError) {
              console.error('Erro no logout automtico:', logoutError);
            }
          }
        } else {
          console.log(` Aguardando mais uma tentativa antes do logout (erros: ${consecutiveErrors.current}/2)`);
        }
      }
    };

    // Verificao inicial aps 10 segundos (mais tempo para o app inicializar)
    console.log(' Agendando verificao inicial de sesso em 10 segundos');
    const initialTimeout = setTimeout(() => {
      if (sessionCheckActive.current && isAuthenticated) {
        console.log(' Executando verificao inicial de sesso');
        checkSession();
      }
    }, 10000); // 10 segundos

    // Verificar a cada 10 minutos (menos agressivo)
    console.log(' Agendando verificaes peridicas a cada 10 minutos');
    const interval = setInterval(() => {
      if (sessionCheckActive.current && isAuthenticated) {
        console.log(' Executando verificao peridica de sesso');
        checkSession();
      }
    }, 10 * 60 * 1000); // 10 minutos

    return () => {
      console.log(' Limpando timers de verificao de sesso');
      clearTimeout(initialTimeout);
      clearInterval(interval);
      sessionCheckActive.current = false;
    };
  }, [isAuthenticated, showWarning, logout, isEditingActive]);

  // Reset quando o usurio no estiver mais autenticado
  useEffect(() => {
    if (!isAuthenticated) {
      console.log(' Usurio no autenticado - resetando estado da sesso');
      setShowWarning(false);
      setTimeLeft(120);
      isLoggingOut.current = false;
      sessionCheckActive.current = false;
      consecutiveErrors.current = 0;
      lastCheckTime.current = 0;
    }
  }, [isAuthenticated]);

  // Funo para estender sesso
  const extendSession = useCallback(async () => {
    if (isLoggingOut.current) {
      console.log(' No  possvel estender sesso - logout em andamento');
      return false;
    }

    try {
      console.log(' Tentando estender sesso...');
      const response = await apiService.post('/auth/extend-session');
      
      if (response.success) {
        console.log(' Sesso estendida pelo usurio');
        setShowWarning(false);
        setTimeLeft(120); // Reset para 2 horas
        consecutiveErrors.current = 0; // Reset erros
        return true;
      } else {
        console.error(' Falha ao estender sesso:', response.message);
        return false;
      }
    } catch (error) {
      console.error(' Erro ao estender sesso:', error);
      return false;
    }
  }, []);

  // Marcar incio de edio
  const startEditing = useCallback((context = 'unknown') => {
    console.log(` Iniciando edio: ${context}`);
    setIsEditingActive(true);
  }, []);

  // Marcar fim de edio
  const stopEditing = useCallback((context = 'unknown') => {
    console.log(` Finalizando edio: ${context}`);
    setIsEditingActive(false);
  }, []);

  // Resetar atividade
  const resetActivity = useCallback(() => {
    console.log(' Atividade resetada pelo usurio');
    if (isAuthenticated && !isLoggingOut.current) {
      setTimeLeft(120);
      setShowWarning(false);
      consecutiveErrors.current = 0;
    }
  }, [isAuthenticated]);

  // Verificar se est no grace period
  const isInGracePeriod = useCallback(() => {
    return isEditingActive;
  }, [isEditingActive]);

  // Debug info para desenvolvimento
  useEffect(() => {
    if (process.env.NODE_ENV === 'development') {
      console.log(' Debug useSessionTimeout:', {
        isAuthenticated,
        showWarning,
        timeLeft,
        isEditingActive,
        isLoggingOut: isLoggingOut.current,
        sessionCheckActive: sessionCheckActive.current,
        consecutiveErrors: consecutiveErrors.current,
        timeSinceLastCheck: lastCheckTime.current ? Date.now() - lastCheckTime.current : 'never'
      });
    }
  }, [isAuthenticated, showWarning, timeLeft, isEditingActive]);

  return {
    showWarning,
    timeLeft,
    isEditingActive,
    isInGracePeriod: isInGracePeriod(),
    extendSession,
    startEditing,
    stopEditing,
    resetActivity,
    // Debug info para desenvolvimento
    debug: process.env.NODE_ENV === 'development' ? {
      consecutiveErrors: consecutiveErrors.current,
      lastCheckTime: lastCheckTime.current,
      sessionCheckActive: sessionCheckActive.current
    } : null
  };
};

export default useSessionTimeout;