// src/pages/RelatoriosPage.jsx - Com filtros por equipe
import React, { useState, useEffect, useCallback } from 'react';
import Header from '../components/common/Header';
import Navigation from '../components/common/Navigation';
import { useNotification } from '../context/NotificationContext';
import { useAuth } from '../context/AuthContext';
import storageService from '../services/storageService';

const RelatoriosPage = () => {
  const { user, getMyTeam, getConsultorName } = useAuth();
  const [loading, setLoading] = useState(false);
  const [estatisticas, setEstatisticas] = useState({
    total: 0,
    totalControle: 0,
    totalUGs: 0,
    ultimaProposta: 'N/A'
  });

  const { showNotification } = useNotification();

  const carregarEstatisticas = useCallback(async () => {
    try {
      setLoading(true);

      let dadosProspec = [];
      let dadosControle = [];
      let dadosUGs = [];

      if (user?.role === 'admin') {
        // Admin v todos os dados
        dadosProspec = await storageService.getProspec();
        dadosControle = await storageService.getControle();
        dadosUGs = await storageService.getUGs();
      } else {
        // Outros usurios veem apenas dados da sua equipe
        const teamNames = getMyTeam().map(member => member.name);
        
        const allProspec = await storageService.getProspec();
        const allControle = await storageService.getControle();
        
        dadosProspec = allProspec.filter(item => 
          teamNames.includes(item.consultor)
        );
        
        dadosControle = allControle.filter(item => 
          teamNames.includes(item.consultor)
        );

        // UGs apenas para admin
        dadosUGs = [];
      }

      // Encontrar ltima proposta
      let ultimaProposta = 'N/A';
      if (dadosProspec.length > 0) {
        const propostas = dadosProspec
          .filter(p => p.data)
          .sort((a, b) => new Date(b.data) - new Date(a.data));
        
        if (propostas.length > 0) {
          ultimaProposta = new Date(propostas[0].data).toLocaleDateString('pt-BR');
        }
      }

      setEstatisticas({
        total: dadosProspec.length,
        totalControle: dadosControle.length,
        totalUGs: dadosUGs.length,
        ultimaProposta
      });

    } catch (error) {
      console.error(' Erro ao carregar estatsticas:', error);
      showNotification('Erro ao carregar estatsticas', 'error');
    } finally {
      setLoading(false);
    }
  }, [user, getMyTeam, showNotification]);

  useEffect(() => {
    carregarEstatisticas();
  }, [carregarEstatisticas]);

  const exportarProspec = async () => {
    try {
      setLoading(true);
      showNotification('Preparando exportao...', 'info');

      if (user?.role === 'admin') {
        // Admin exporta todos os dados com consultor responsvel
        const allData = await storageService.getProspec();
        const dataWithResponsible = allData.map(item => ({
          ...item,
          consultor: getConsultorName(item.consultor)
        }));
        
        await storageService.exportarDadosFiltrados('prospec', dataWithResponsible);
      } else {
        // Outros usurios exportam apenas dados da equipe
        const teamNames = getMyTeam().map(member => member.name);
        const allData = await storageService.getProspec();
        const filteredData = allData.filter(item => 
          teamNames.includes(item.consultor)
        );
        
        await storageService.exportarDadosFiltrados('prospec', filteredData);
      }

      showNotification('Relatrio de prospeco exportado com sucesso!', 'success');
    } catch (error) {
      console.error(' Erro ao exportar prospec:', error);
      showNotification('Erro ao exportar: ' + error.message, 'error');
    } finally {
      setLoading(false);
    }
  };

  const exportarControle = async () => {
    try {
      setLoading(true);
      showNotification('Preparando exportao...', 'info');

      if (user?.role === 'admin') {
        // Admin exporta todos os dados com consultor responsvel
        const allData = await storageService.getControle();
        const dataWithResponsible = allData.map(item => ({
          ...item,
          consultor: getConsultorName(item.consultor)
        }));
        
        await storageService.exportarDadosFiltrados('controle', dataWithResponsible);
      } else {
        // Outros usurios exportam apenas dados da equipe
        const teamNames = getMyTeam().map(member => member.name);
        const allData = await storageService.getControle();
        const filteredData = allData.filter(item => 
          teamNames.includes(item.consultor)
        );
        
        await storageService.exportarDadosFiltrados('controle', filteredData);
      }

      showNotification('Relatrio de controle exportado com sucesso!', 'success');
    } catch (error) {
      console.error(' Erro ao exportar controle:', error);
      showNotification('Erro ao exportar: ' + error.message, 'error');
    } finally {
      setLoading(false);
    }
  };

  const exportarUGs = async () => {
    if (user?.role !== 'admin') {
      showNotification('Apenas administradores podem exportar UGs', 'warning');
      return;
    }

    try {
      setLoading(true);
      showNotification('Preparando exportao...', 'info');
      
      await storageService.exportarParaCSV('ugs');
      showNotification('Relatrio de UGs exportado com sucesso!', 'success');
    } catch (error) {
      console.error(' Erro ao exportar UGs:', error);
      showNotification('Erro ao exportar: ' + error.message, 'error');
    } finally {
      setLoading(false);
    }
  };

  const carregarDados = async () => {
    try {
      setLoading(true);
      showNotification('Atualizando dados...', 'info');
      await carregarEstatisticas();
      showNotification('Dados atualizados com sucesso!', 'success');
    } catch (error) {
      console.error(' Erro ao atualizar dados:', error);
      showNotification('Erro ao atualizar dados', 'error');
    } finally {
      setLoading(false);
    }
  };

  const limparTodosDados = async () => {
    if (user?.role !== 'admin') {
      showNotification('Apenas administradores podem limpar dados', 'warning');
      return;
    }

    const confirmacao = window.confirm(
      ' ATENO: Esta ao ir remover TODOS os dados do sistema!\n\n' +
      'Isso inclui:\n' +
      ' Todas as propostas\n' +
      ' Todos os dados de controle\n' +
      ' Todas as UGs cadastradas\n' +
      ' Todos os usurios (exceto admin)\n\n' +
      'Esta ao NO PODE ser desfeita!\n\n' +
      'Deseja realmente continuar?'
    );

    if (!confirmacao) return;

    const confirmacaoFinal = window.confirm(
      'ltima confirmao: Tem CERTEZA ABSOLUTA que deseja limpar todos os dados?'
    );

    if (!confirmacaoFinal) return;

    try {
      setLoading(true);
      showNotification('Limpando todos os dados...', 'info');

      // Limpar todos os dados
      localStorage.removeItem('aupus_prospec');
      localStorage.removeItem('aupus_controle');
      localStorage.removeItem('aupus_ugs');
      localStorage.removeItem('aupus_users');
      localStorage.removeItem('aupus_calibragem_global');

      await carregarEstatisticas();
      showNotification('Todos os dados foram removidos com sucesso!', 'success');
    } catch (error) {
      console.error(' Erro ao limpar dados:', error);
      showNotification('Erro ao limpar dados: ' + error.message, 'error');
    } finally {
      setLoading(false);
    }
  };

  const isAdmin = user?.role === 'admin';

  return (
    <div className="page-container">
      <div className="container">
        <Header 
          title="Exportao e Anlise de Dados" 
        />
        <Navigation />

        {/* Estatsticas Resumidas */}
        <section className="quick-stats">
          <div className="stat-card">
            <div className="stat-icon"></div>
            <div className="stat-content">
              <span className="stat-label">Propostas</span>
              <span className="stat-value">{estatisticas.total}</span>
            </div>
          </div>
          
          <div className="stat-card">
            <div className="stat-icon"></div>
            <div className="stat-content">
              <span className="stat-label">No Controle</span>
              <span className="stat-value">{estatisticas.totalControle}</span>
            </div>
          </div>

          {isAdmin && (
            <div className="stat-card">
              <div className="stat-icon"></div>
              <div className="stat-content">
                <span className="stat-label">UGs</span>
                <span className="stat-value">{estatisticas.totalUGs}</span>
              </div>
            </div>
          )}

          <div className="stat-card">
            <div className="stat-icon"></div>
            <div className="stat-content">
              <span className="stat-label">ltima Proposta</span>
              <span className="stat-value" style={{ fontSize: '1rem' }}>
                {estatisticas.ultimaProposta}
              </span>
            </div>
          </div>
        </section>

        {loading && (
          <div className="loading-section">
            <div className="loading-spinner"></div>
            <p>Processando...</p>
          </div>
        )}

        {!loading && (
          <>
            {/* Exportar Relatrios */}
            <section className="filters-section">
              <div className="filters-container">
                <h2> Exportar Relatrios</h2>
                <div className="export-grid">
                  <div className="export-card">
                    <h3>Relatrio de Prospeco</h3>
                    <p>
                      {isAdmin 
                        ? 'Lista completa de propostas com status, consultores e dados tcnicos'
                        : 'Propostas da sua equipe com status e dados tcnicos'
                      }
                    </p>
                    <button 
                      onClick={exportarProspec} 
                      className="btn btn-primary"
                      disabled={estatisticas.total === 0}
                    >
                       Exportar Prospeco
                    </button>
                  </div>
                  
                  <div className="export-card">
                    <h3>Relatrio de Controle</h3>
                    <p>
                      {isAdmin 
                        ? 'Propostas fechadas com UGs definidas e status de calibragem'
                        : 'Propostas fechadas da sua equipe com UGs definidas'
                      }
                    </p>
                    <button 
                      onClick={exportarControle} 
                      className="btn btn-primary"
                      disabled={estatisticas.totalControle === 0}
                    >
                       Exportar Controle
                    </button>
                  </div>
                  
                  {isAdmin && (
                    <div className="export-card">
                      <h3>Relatrio de UGs</h3>
                      <p>Dados tcnicos das Unidades Geradoras e capacidades</p>
                      <button 
                        onClick={exportarUGs} 
                        className="btn btn-primary"
                        disabled={estatisticas.totalUGs === 0}
                      >
                         Exportar UGs
                      </button>
                    </div>
                  )}
                </div>
              </div>
            </section>

            {/* Aes do Sistema - apenas para admin */}
            {isAdmin && (
              <section className="filters-section">
                <div className="filters-container">
                  <h2> Aes do Sistema</h2>
                  <div className="actions-container">
                    <button onClick={carregarDados} className="btn btn-secondary">
                       Atualizar Dados
                    </button>
                    <button onClick={limparTodosDados} className="btn btn-danger">
                       Limpar Todos os Dados
                    </button>
                  </div>
                  
                  <div className="system-info">
                    <h4> Informaes do Sistema</h4>
                    <div className="info-grid">
                      <div className="info-item">
                        <strong>ltima Proposta:</strong> {estatisticas.ultimaProposta}
                      </div>
                      <div className="info-item">
                        <strong>Total de Registros:</strong> {estatisticas.total + estatisticas.totalControle + estatisticas.totalUGs}
                      </div>
                      <div className="info-item">
                        <strong>Storage:</strong> localStorage (local no navegador)
                      </div>
                      <div className="info-item">
                        <strong>Status:</strong>  Sistema Operacional
                      </div>
                    </div>
                  </div>
                </div>
              </section>
            )}

            {/* Informaes da Equipe para no-admins */}
            {!isAdmin && (
              <section className="filters-section">
                <div className="filters-container">
                  <h2> Informaes da Equipe</h2>
                  <div className="team-info">
                    <div className="info-grid">
                      <div className="info-item">
                        <strong>Seu Perfil:</strong> {user?.name} ({user?.role})
                      </div>
                      <div className="info-item">
                        <strong>Membros da Equipe:</strong> {getMyTeam().length}
                      </div>
                      <div className="info-item">
                        <strong>Propostas da Equipe:</strong> {estatisticas.total}
                      </div>
                      <div className="info-item">
                        <strong>No Controle:</strong> {estatisticas.totalControle}
                      </div>
                    </div>
                  </div>
                </div>
              </section>
            )}
          </>
        )}
      </div>
    </div>
  );
};

export default RelatoriosPage;